import React, { useState } from "react";
import { Link } from "react-router-dom";

import InputItem from "../../components/client/InputItem";
import { useHistory } from "react-router";
import swal from "sweetalert";
import authApi from "../../api/authApi";
import axios from "axios";
import logo from "../../assets/Image/footer-logo_1.png";

const Register = () => {
  const history = useHistory();
  const [register, setRegister] = useState({
    username: "",
    email: "",
    password: "",
    confirm_password: "",
    message: [],
  });
  const handleInput = (e) => {
    setRegister({ ...register, [e.target.name]: e.target.value });
  };

  const registerSubmit = (e) => {
    e.preventDefault();
    const data = {
      username: register.username,
      email: register.email,
      password: register.password,
      confirm_password: register.confirm_password,
    };

    axios
      .get(`${process.env.REACT_APP_API_URL}/sanctum/csrf-cookie`)
      .then((response) => {
        authApi.postRegister(data).then((res) => {
          if (res.data.status === 200) {
            localStorage.setItem("auth_token", res.data.token);
            localStorage.setItem("auth_name", res.data.username);
            swal("Success", res.data.message, "success");
            history.push("/");
          } else {
            setRegister({ ...register, message: res.data.validation_errors });
          }
        });
      });
  };

  return (
    <div className="auth">
      <div className="auth__container">
        <div className="auth__logo">
          <Link to="/">
            <img src={logo} alt="logo" />
          </Link>
        </div>
        <div className="auth__title">
          <h3>To Ti Khon</h3>
          <p>Vui lng to ti khon  tip tc!</p>
        </div>
        <form className="auth__form">
          <InputItem
            label="Tn ng Nhp"
            placeholder="Nhp tn ng nhp ca bn"
            type="text"
            icon="bx bx-user"
            name="username"
            onChange={handleInput}
            value={register.username}
            message={register.message.username}
          />
          <InputItem
            label="Email"
            placeholder="Nhp email ca bn"
            type="email"
            icon="bx bx-envelope"
            name="email"
            onChange={handleInput}
            value={register.email}
            message={register.message.email}
          />
          <InputItem
            label="Mt Khu"
            placeholder="Nhp mt khu ca bn"
            type="password"
            icon="bx bx-lock-alt"
            name="password"
            onChange={handleInput}
            value={register.password}
            message={register.message.password}
          />
          <InputItem
            label="Xc Nhn Mt Khu"
            placeholder="Nhp mt khu xc nhn ca bn"
            type="password"
            icon="bx bx-lock-alt"
            name="confirm_password"
            onChange={handleInput}
            value={register.confirm_password}
            message={register.message.confirm_password}
          />
          <button className="auth__form__submit" onClick={registerSubmit}>
            ng K
          </button>
        </form>
        <div className="auth__bottom">
          <p>
            Bn  c ti khon? <Link to="/login">ng Nhp</Link>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Register;
