import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useHistory } from "react-router";
import swal from "sweetalert";
import InputItem from "../../components/client/InputItem";
import authApi from "../../api/authApi";
import axiosClient from "../../api/axiosClient";
import axios from "axios";
import "antd/dist/antd.css";
import logo from "../../assets/Image/footer-logo_1.png";
import Helmet from "../../components/Helmet";

const Login = () => {
  const history = useHistory();
  const [login, setLogin] = useState({
    username: "",
    password: "",
    message: [],
  });

  const handleInput = (e) => {
    setLogin({ ...login, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const data = {
      username: login.username,
      password: login.password,
    };
    axios
      .get(`${process.env.REACT_APP_API_URL}/sanctum/csrf-cookie`)
      .then((response) => {
        authApi.postLogin(data).then((res) => {
          console.log(res);
          if (res.data.status === 200) {
            localStorage.setItem("auth_token", res.data.token);
            localStorage.setItem("auth_name", res.data.username);
            swal("Success", res.data.message, "success");
            history.push("/");
          } else {
            if (res.data.status === 401) {
              swal("Warning", res.data.message, "warning");
            } else {
              setLogin({ ...login, message: res.data.validation_errors });
            }
          }
        });
      });
  };
  return (
    <Helmet title="Login">
      <div className="auth">
        <div className="auth__container">
          <div className="auth__logo">
            <Link to="/">
              <img src={logo} alt="logo" />
            </Link>
          </div>
          <div className="auth__title">
            <h3>ng Nhp</h3>
            <p>Vui Lng ng Nhp  Tip Tc!</p>
          </div>
          <form className="auth__form" onSubmit={handleSubmit}>
            <InputItem
              label="Email hoc tn ng nhp"
              placeholder="Nhp email hoc tn ng nhp ca bn"
              type="text"
              icon="bx bx-user"
              name="username"
              onChange={handleInput}
              value={login.username}
              message={login.message.username}
            />
            <InputItem
              label="Mt Khu"
              placeholder="Mt khu ca bn"
              type="password"
              icon="bx bx-lock-alt"
              name="password"
              onChange={handleInput}
              value={login.password}
              message={login.message.password}
            />

            <div className="auth__form__forgot">
              <Link to="/forgot-password">Qun Mt Khu?</Link>
            </div>
            <button type="submit" className="auth__form__submit">
              ng Nhp
            </button>
          </form>
          <div className="auth__bottom">
            Bn Cha C Ti Khon? <Link to="/register">To Ti Khon</Link>
          </div>
        </div>
      </div>
    </Helmet>
  );
};

export default Login;
